
import { defineComponent } from "vue";
import { routeStore } from "@/stores/route.store";

export default defineComponent({
  name: "NavBar",
  components: {},
  computed: {
    currentRouteName() {
      return this.$route.fullPath;
    },
  },
  data() {
    return {
      routeStore: routeStore,
    };
  },
  methods: {
    signOut() {
      this.$emit("signOut");
    },
  },
});
